
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

h1[data-v-d14401cc], h2[data-v-d14401cc] {
  font-weight: normal;
}
ul[data-v-d14401cc] {
  list-style-type: none;
  padding: 0;
}
li[data-v-d14401cc] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-d14401cc] {
  color: #42b983;
}

.wrap {
  overflow: hidden;
}
.color-folder {
  color: #FE9E0F;
}
.color-file {
  color: #ccc;
}
.color-green {
  color: #00B996;
}
.w200 {
  width: 200px;
}
.hover-bg:hover {
  background-color: #f2f2f2;
}
.left-menu {
  width: 200px;
}
.left-menu li {
    cursor: pointer;
}
.left-menu .new-folder-input {
    width: 110px;
    padding: 5px 10px;
    border: 1px #cdcdcd solid;
    border-radius: 2px;
}
.left-menu .new-folder-input:focus {
      outline: 1px #22a7f0 solid;
      border-radius: 2px;
}
.left-menu .menu-top ul {
    font-size: 14px;
    line-height: 30px;
    color: #666;
    margin-bottom: 10px;
}
.left-menu .menu-top ul li:first-child:before {
      height: 15px;
      top: 0;
}
.left-menu .menu-top ul li {
      position: relative;
      padding-left: 35px;
      padding-right: 10px;
}
.left-menu .menu-top ul li:before {
        content: '';
        position: absolute;
        width: 13px;
        height: 30px;
        border-bottom: 1px #999 dashed;
        border-left: 1px #999 dashed;
        left: 20px;
        top: -15px;
}
.right-content {
  left: 200px;
}
.right-content .title input {
    border: none;
    width: 45%;
}
.right-content .title input:focus {
      outline: none;
}
